export default "<b>ഗുരുവായൂർ ദേവസ്വം മുഖ്യമന്ത്രിയുടെ ദുരിതാശ്വാസനിധിയിലേക്ക് നൽകിയ പത്ത് കോടി രൂപ ഉടനടി തിരിച്ചു നൽകണമന്ന് ഹൈക്കോടതി ഉത്തരവ്.</b><br><br>ദുരിതാശ്വാസനിധിയിലേക്ക് ദേവസ്വം പണം നല്കിയത് നിയമവിരുദ്ധമെന്നാണ് ഹൈക്കോടതിയുടെ ഫുള് ബഞ്ച് വിധിച്ചിരിക്കുന്നത്. ട്രസ്റ്റി എന്ന നിലയില് സ്വത്തുവകകള് പരിപാലിക്കാന് മാത്രമേ ദേവസ്വം ബോര്ഡിന് അവകാശമുള്ളൂ. അത് വേറാര്ക്കും കൈമാറാന് അവകാശമില്ല. <b>ഗുരുവായൂര് ക്ഷേത്രവുമായി ബന്ധപ്പെട്ട എല്ലാ സ്വത്തുവകകളുടെയും അവകാശി ഗുരുവായൂരപ്പനാണെന്ന്</b> ഹൈക്കോടതിയുടെ ഫുള്ബഞ്ച് ഉത്തരവില് നിരീക്ഷിക്കുന്നു.<br><br>- ഏഷ്യനെറ്റ് ന്യൂസ്<br><br><a href=\"https://www.asianetnews.com/kerala-news/money-given-by-guruvayoor-devaswom-board-to-cm-relief-fund-should-be-reimbursed-orders-kerala-high-court-qlj4p2\">https://www.asianetnews.com/kerala-news/money-given-by-guruvayoor-devaswom-board-to-cm-relief-fund-should-be-reimbursed-orders-kerala-high-court-qlj4p2</a>"
